var that;
import data from '../data.js';
export default {
	data() {
		return { 
			page: 1,
			list: [],
			nolist: false,
			nomore: false,
			lodstatus: '' 
		};
	},
	methods: {
		tp(e) {
			uni.navigateTo({
				url: '/pages/goods/goodsGroup/goodsGroup?gid=' + e.currentTarget.dataset.id
			});
		},
		getlist(i) {
			if (that.nomore) {
				return;
			}
			that.lodstatus = 'loading';
			that.nolist = false;
			this.$request
				.request({
					url: that.api.group.list,
					data: { 
						page: that.page,
						cid:0
					},
					success(res){
						let list=res.list; 
						that.lodstatus = '';
						if (list.length > 0) {
							that.list = that.list.concat(list);
							that.page = that.page + 1;
							that.lodstatus = 'more';
							that.list.length == 0 ? (that.nolist = true) : (that.nolist = false);
							list.length == 0 ? ((that.lodstatus = ''), (that.nolist = true)) : list.length > 10 ? (that.lodstatus = 'more') : (that.lodstatus = '');
						} else {
							if (that.page != 1) {
								that.nomore = true;
								that.lodstatus = 'noMore';
							} else {
								that.nolist = true;
							}
						}
						  console.log(that.list);
					}
				}) 
		}
	},
	onLoad() {
		that = this; 
		that.getlist();
	}
};
